import { useEffect } from "react";
import { useWPAjax } from "../../../../wp-utils/v1";

const InitializeActivation = ({ info, nonce }) => {
  const { install_secret_key, install_public_key, install_id, pending_activation = false, fs_action, is_skip_activation } = info;
  const { data, refetch } = useWPAjax("fs_init", { nonce, info, site: { install_id, public_key: install_public_key, secret_key: install_secret_key } }, true);
  //   const { data: user_data, refetch: fetchUserData } = useWPAjax("fs_api_request", {}, true);

  useEffect(() => {
    console.log(info);
    window.initData = data;
    if (data) {
      // window.location.href = data.admin_url + data?.config?.menu?.["first-path"] || window.location.origin + window.location.pathname;
      console.log(data.admin_url + data?.config?.menu?.["first-path"]);
    }
    console.log({ data });
  }, [data]);

  useEffect(() => {
    // window.history.pushState("object or string", "Title", window.location.origin + window.location.pathname);
    const data = {};
    if (pending_activation) {
      data.notice = {
        activation_pending: {
          message: `You should receive a confirmation email for {name} to your mailbox at {email}. Please make sure you click the button in that email to complete the opt-in.`,
          title: "Thanks!",
          type: "success",
          id: "activation_pending",
          dismissible: true,
          sticky: true,
        },
      };
      console.log({ data });
    } else {
      // window.history.pushState("object or string", "Title", data.admin_url + data?.config?.menu?.["first-path"]);
      console.log({ is_skip_activation });
    }
    refetch(data);
  }, []);

  //   console.log(info);
  return <></>;
};

export default InitializeActivation;
